html {
  height: 100%;
}
body {
  padding: 0;
  margin: 0;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2"), url("../fonts/oswaldregular.woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2"), url("../fonts/oswaldmedium.woff");
  font-display: swap;
}
.page-footer {
  display: flex;
  flex-direction: column;
  max-width: 320px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .page-footer {
    max-width: 1440px;
  }
}
.page-footer__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 38px 20px 22px;
}
@media (min-width: 768px) {
  .page-footer__wrapper {
    flex-direction: row;
    justify-content: space-between;
    padding: 58px 30px 55px;
  }
}
@media (min-width: 1440px) {
  .page-footer__wrapper {
    padding: 57px 110px 58px;
  }
}
.page-footer__link {
  padding-bottom: 27px;
  cursor: pointer;
}
.page-footer__link:hover {
  opacity: 0.8;
}
.page-footer__link:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .page-footer__link {
    padding: 0;
    display: block;
  }
}
@media (min-width: 1440px) {
  .page-footer__link img {
    display: block;
  }
}
.page-footer__academy-container {
  display: flex;
  align-items: center;
  padding: 20px 0 0;
}
@media (min-width: 768px) {
  .page-footer__academy-container {
    padding: 0;
  }
}
.page-footer__academy-link {
  font-family: "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #444444;
  text-decoration: none;
  margin-right: 139px;
}
.page-footer__academy-link:hover {
  color: rgba(69, 69, 69, 0.3);
}
@media (min-width: 768px) {
  .page-footer__academy-link {
    margin-right: 22px;
    width: 114px;
  }
}
@media (min-width: 768px) {
  .page-footer__academy-link-logo svg {
    display: block;
  }
}
.page-footer__academy-link-logo:hover svg {
  fill: #68B738;
}
.page-footer__academy-link-logo:active svg {
  fill: rgba(104, 183, 56, 0.3);
}
.social-container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 22px 0 18px;
  width: 100%;
}
@media (min-width: 768px) {
  .social-container {
    padding: 0;
  }
}
.social-container__list {
  display: flex;
  align-items: baseline;
  margin: 0;
  padding: 0;
  list-style-type: none;
  min-width: 133px;
}
.social-container__list::before {
  position: absolute;
  right: 0;
  left: 0;
  height: 1px;
  bottom: 0;
  background-color: #d9d9d9;
  content: "";
}
.social-container__list::after {
  position: absolute;
  right: 0;
  left: 0;
  height: 1px;
  bottom: 0;
  top: 0;
  background-color: #d9d9d9;
  content: "";
}
@media (min-width: 768px) {
  .social-container__list {
    margin-left: 25px;
    justify-content: space-between;
    align-items: center;
  }
  .social-container__list::before {
    display: none;
  }
  .social-container__list::after {
    display: none;
  }
}
@media (min-width: 1440px) {
  .social-container__list {
    margin-left: 30px;
  }
}
.social-container__item {
  margin-right: 36px;
}
@media (min-width: 768px) {
  .social-container__item {
    margin: 0;
  }
}
.social-container__item:last-child {
  margin-right: 5px;
}
.social-container__item:last-child:hover .social-container__image {
  stroke: #68B738;
  fill: none;
}
.social-container__item:last-child:active .social-container__image {
  stroke: rgba(104, 183, 56, 0.3);
}
.social-container__link:hover svg {
  fill: #68B738;
}
.social-container__link:active .social-container__image {
  fill: rgba(104, 183, 56, 0.3);
}
@media (min-width: 768px) {
  .social-container__item--vk svg {
    width: 30px;
    height: 32px;
  }
}
@media (min-width: 768px) {
  .social-container__item--instagram svg {
    width: 23px;
    height: 23px;
  }
}
@media (min-width: 768px) {
  .social-container__item--facebook svg {
    width: 25px;
    height: 30px;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 65px;
  padding: 0 20px;
  margin: 0 auto;
  box-sizing: border-box;
}
@media (max-width: 767px) {
  .page-header {
    border-bottom: 1px solid #d9d9d9;
  }
}
@media (min-width: 768px) {
  .page-header {
    align-items: flex-end;
    padding: 0 30px;
    min-height: 76px;
    max-width: 1440px;
  }
}
@media (min-width: 1440px) {
  .page-header {
    padding: 47px 110px 30px;
    max-width: 1440px;
  }
}
@media (max-width: 767px) {
  .page-header--nojs {
    flex-wrap: wrap;
  }
}
.page-header__logo-container {
  display: flex;
  align-items: center;
  min-height: 66px;
}
@media (min-width: 768px) {
  .page-header__logo-container {
    align-items: flex-end;
  }
}
.page-header__logo {
  margin-right: 55px;
}
.page-header__logo:nth-child(2) {
  margin-right: 58px;
}
.page-header__logo:nth-child(2) img {
