}
@media (min-width: 768px) {
  .page-header__logo:last-child {
    display: none;
  }
}
.page-header__logo-image:hover {
  opacity: 0.8;
}
.page-header__logo-image:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .page-header__logo-image {
    content: url("../img/page-header-logo-tablet.svg");
    width: 174px;
    height: 52px;
    display: block;
  }
}
@media (min-width: 1440px) {
  .page-header__logo-image {
    content: url("../img/page-header-logo-desk.svg");
    width: 202px;
    height: 59px;
    display: block;
  }
}
.main-nav {
  display: flex;
  justify-content: center;
  align-items: stretch;
  right: 12px;
  width: 100%;
}
@media (min-width: 768px) {
  .main-nav {
    justify-content: flex-end;
    position: static;
  }
}
.main-nav--closed .main-nav__toggle::before {
  position: absolute;
  content: "";
  top: 10px;
  left: 8px;
  right: 10px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
@media (max-width: 767px) {
  .main-nav__wrapper {
    position: absolute;
    top: 65px;
    left: 0;
    width: 100%;
    z-index: 1;
  }
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__wrapper {
    display: none;
  }
}
.main-nav--opened .main-nav__wrapper {
  display: block;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__wrapper {
  display: block;
  width: 100%;
}
@media (max-width: 767px) {
  .main-nav--nojs .main-nav__wrapper {
    position: static;
  }
}
@media (max-width: 767px) {
  .main-nav--nojs .main-nav__item {
    padding: 23px 49px;
  }
}
.main-nav--opened .main-nav__toggle {
  width: 40px;
  height: 40px;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 18px;
  left: 8px;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav__toggle {
  position: relative;
  width: 40px;
  height: 40px;
  border: none;
  cursor: pointer;
  background-color: transparent;
  margin-left: auto;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__list {
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  background-color: #ffffff;
  top: 65px;
  left: 0;
  width: 100%;
  z-index: 1;
}
@media (min-width: 768px) {
  .main-nav__list {
    position: static;
    top: 0;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
  }
}
@media (min-width: 1440px) {
  .main-nav__list--background {
    background-color: #68B738;
  }
  .main-nav__list--background::before {
    position: absolute;
    top: 0;
    left: 50%;
    width: 720px;
    height: 694px;
    background-image: url("../img/index-background-desktop.png");
    background-repeat: no-repeat;
    content: "";
  }
  .main-nav__list--background::after {
    position: absolute;
    top: 0;
    left: 50%;
    width: 50%;
    height: 694px;
    background-color: #62a53a;
    content: "";
    z-index: -1;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .main-nav__list--background::before {
    background-image: url("../img/index-background-desktop@2x.png");
  }
}
.main-nav__item {
  position: relative;
  padding: 22px 69px 23px;
  border-top: 1px solid #e6e6e6;
  box-sizing: border-box;
}
.main-nav__item:last-child {
