}
.main-nav__item--current {
  position: relative;
}
@media (min-width: 768px) {
  .main-nav__item--current::before {
    position: absolute;
    top: 32px;
    left: 1px;
    width: 100%;
    height: 2px;
    background-color: #68B738;
    content: "";
  }
}
@media (min-width: 1440px) {
  .main-nav__item--current::before {
    background-color: #ffffff;
